import React,{Component} from 'react';
import {Link} from 'react-router-dom'; 
import BlogSidebar from '../components/BlogSidebar'; 
 
class BlogPagTwo extends Component{

    state = {
        heading: 'Latest news',
        subHeading: 'articles & tips',
    } 

    render(){

    const blogListArray = [
        {
            image:'blog4.jpg',
            commentCount:'0 comments',
            date:'6 Nov 2019', 
            title:'How to Find and Afford Organic Food in Your Area', 
            postExcerpt:'Conventional farming methods expose produce to chemicals in the form of pesticides, fertilisers, and preser vatives. While these greatly improve productivity, they can be very harmful to human beings, and in large amou nts even cause irreversible damage. Organic food on the other hand, is produced through traditional farming methods, without the use of any artificial compounds or preservatives, making it far safer for human consump tion human beings, and in large amou nts even cause irreversible.', 
            link: '/single-blog',
        },
         {
            image:'blog5.jpg',
            commentCount:'0 comments',
            date:'6 Nov 2019', 
            title:'How to Find and Afford Organic Food in Your Area', 
            postExcerpt:'Conventional farming methods expose produce to chemicals in the form of pesticides, fertilisers, and preser vatives. While these greatly improve productivity, they can be very harmful to human beings, and in large amou nts even cause irreversible damage. Organic food on the other hand, is produced through traditional farming methods, without the use of any artificial compounds or preservatives, making it far safer for human consump tion human beings, and in large amou nts even cause irreversible.', 
            link: '/single-blog',
        },
    ]

 
    const blogListtMap = blogListArray.map((valu, i) => {
        return(
            <div class="col-sm-12" key={i}> 
                <div class="blog_wrp">
                    <a class="blog_img" href="blog.html">
                        <img src={`assets/images/${valu.image}`} alt="" />
                    </a>
                    <div class="blog_info">
                        <div class="blog_date"> 
                            <span><i class="fa fa-comment-o"></i>{valu.commentCount} </span>
                            <span> <i class="fa fa-calendar"></i>{valu.date}</span>
                        </div>
                        <Link to={`${valu.link}`}>
                            <h4>{valu.title}</h4> 
                        </Link> 
                        <p>{valu.postExcerpt}</p> 
                        <Link to={`${valu.link}`} class="more-link">Read more </Link> 
                    </div>
                </div>
            </div>
        )
    });


    return(
	    <div className="blog-page-two">

			{/*====================  breadcrumb area ====================*/}
			<header id="page-top" className="blog-banner"> 
			  <div className="container" id="blog">
			      <div className="row blog-header text-center">
			          <div className="col-sm-12"> 
			              <h3>Latest Blog</h3>
			              <h4><a href="index.html"> Home </a> &gt; Blog Two</h4>
			          </div>
			      </div> 
			  </div> 
			</header>
			{/*====================  End of breadcrumb area  ====================*/} 

	        <div className="blog_container">
	            <div className="container"> 
	                <div className="row">     

                        <div className="col-md-9 col-sm-12 blog-area">
                            <div className="row">

                                {/* Blog Item */}
                                {blogListtMap} 
         

                                {/* Blog Pagination */}
                                <div className="prodt_pagination">
                                    <ul>
                                        <li><a href="blog-two" className="page_number current">1</a></li>
                                        <li><a href="blog-two" className="page_number">2</a></li>
                                        <li><a href="blog-two" className="page_number">→</a></li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                        {/* Blog Area */}


                        {/*  Widget Area */} 
                        
                        <BlogSidebar />
 
                        {/* End: Widget Area */} 

	                </div> 
	            </div> 
	        </div>


		</div> 

        )

    }
}

 
export default BlogPagTwo;