import React , {Component } from 'react'; 
import {Link} from 'react-router-dom'; 

class About extends Component{
    
    render(){
        const productArray = [
            {
                image:'product_1.png',
                title:' Juicy Grapes ',
                price:'$46.00',
                sales:'6%',
            },
             {
                image:'product_2.png',
                title:' Red Watermelon ',
                price:'$56.00',
                sales:'',
            },
            {
                image:'product_3.png',
                title:' Juicy Orange ',
                price:'$46.00',
                sales:'',
            },
            {
                image:'product_4.png',
                title:' Fresh Banana ',
                price:'$46.00',
                sales:'5%',
            },
            {
                image:'product_1.png',
                title:' Juicy Grapes ',
                price:'$46.00',
                sales:'6%',
            },
             {
                image:'product_2.png',
                title:' Red Watermelon ',
                price:'$56.00',
                sales:'',
            },
            {
                image:'product_3.png',
                title:' Juicy Orange ',
                price:'$46.00',
                sales:'',
            },
            {
                image:'product_4.png',
                title:' Fresh Banana ',
                price:'$46.00',
                sales:'5%',
            },
        ]
     
        const productMap = productArray.map((valu, i) => {
            return(

                <div className="col-md-3 col-sm-12" key={i}> 
                    <div className="product_wrp">
                        <div className="product_img"> 
                            <img src={`assets/images/${valu.image}`} alt="product" />
                            <div className="on_sale">
                                <span>{valu.sales}</span>
                            </div>
                        </div>
                        <div className="product_info">
                            <h4>{valu.title}</h4> 
                            <ul className="product_rating">
                                <li><i className="icofont-star"></i></li>
                                <li><i className="icofont-star"></i></li>
                                <li><i className="icofont-star"></i></li>                               
                                <li><i className="icofont-star"></i></li>                                
                                <li><i className="icofont-star"></i></li>
                            </ul>
                            <span className="product_price">{valu.price}</span>
                        </div>
                        <div className="project_view">
                            <Link to="/"><i className="icon-glyph-13"></i></Link>
                            <Link to="/" className="project-link"><i className="icon-glyph-52"></i></Link>
                        </div>
                    </div>
                </div>
            )
        });

        return(
           <div className="shop-page">
                {/*====================  breadcrumb area ====================*/}
                <header id="page-top" className="blog-banner"> 
                    <div className="container" id="blog">
                        <div className="row blog-header text-center">
                            <div className="col-sm-12"> 
                                <h3> Our Products </h3>
                                <h4><a href="index.html"> Home </a> &gt; Shop </h4>
                            </div>
                        </div> 
                    </div> 
                </header>
                {/*====================  End of breadcrumb area  ====================*/} 

 
                {/*==================== Team Mamber area  ====================*/} 

                <section className="product-section">
                    <div className="container">
                        <div className="row">
                            <div className="col-sm-12">
                                <p className="product_count">Showing 1–8 of 12 results</p>
                                <form className="product_ordering">
                                    <select name="orderby" className="orderby">
                                        <option value="menu_order" selected="selected">Default sorting</option>
                                        <option value="popularity">Sort by popularity</option>
                                        <option value="rating">Sort by average rating</option>
                                        <option value="date">Sort by newness</option>
                                        <option value="price">Sort by price: low to high</option>
                                        <option value="price-desc">Sort by price: high to low</option>
                                    </select>
                                </form>
                            </div>
                        </div>
                        <div className="row">

                            {productMap}

                            <div className="prodt_pagination">
                                <ul>
                                    <li><a href="/" className="page_number current">1</a></li>
                                    <li><a href="/" className="page_number">2</a></li>
                                    <li><a href="/" className="page_number">→</a></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </section>
                {/*====================  End Team Mamber area  ====================*/}  

            </div>
        );
    }
}

export default About;   